
"use client";

import { AppWindow, Github, ExternalLink } from 'lucide-react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { useLanguage } from "@/context/language-context";
import { useState, useEffect } from "react";
import { Skeleton } from "@/components/ui/skeleton";
import { motion } from "framer-motion";

interface Project {
  id: number;
  name: string;
  description: string;
  html_url: string;
  homepage: string;
}

export default function ProjectsPage() {
  const { translations } = useLanguage();
  const [projects, setProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchProjects() {
      try {
        const response = await fetch("https://api.github.com/users/matheussricardoo/repos");
        if (!response.ok) {
          throw new Error('Failed to fetch projects');
        }
        const data = await response.json();
        setProjects(data);
      } catch (err) {
        if (err instanceof Error) {
            setError(err.message);
        } else {
            setError("An unknown error occurred");
        }
      } finally {
        setLoading(false);
      }
    }
    fetchProjects();
  }, []);

  const fadeInAnimation = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    transition: { duration: 0.5, delay: 0.2 }
  };

  return (
    <motion.section 
      id="projects" 
      className="py-16 sm:py-24 h-full"
      initial="initial"
      animate="animate"
      variants={fadeInAnimation}
    >
      <div className="container">
        <div className="flex items-center gap-4 mb-12">
            <AppWindow className="w-8 h-8 text-primary" />
            <h2 className="font-headline text-3xl sm:text-4xl font-bold">{translations.projects.title}</h2>
        </div>
        
        {loading && (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {Array.from({ length: 3 }).map((_, i) => (
              <Card key={i} className="flex flex-col">
                <CardHeader>
                  <Skeleton className="h-6 w-3/4" />
                </CardHeader>
                <CardContent>
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-4 w-full mt-2" />
                  <Skeleton className="h-4 w-1/2 mt-2" />
                </CardContent>
                <CardFooter>
                  <Skeleton className="h-10 w-24" />
                  <Skeleton className="h-10 w-24 ml-2" />
                </CardFooter>
              </Card>
            ))}
          </div>
        )}

        {error && <p className="text-destructive text-center">{error}</p>}

        {!loading && !error && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {projects.map((project) => (
                <Card key={project.id} className="flex flex-col">
                <CardHeader>
                    <CardTitle className="font-headline text-xl">{project.name}</CardTitle>
                </CardHeader>
                <CardContent className="flex-grow">
                    <CardDescription>{project.description || 'No description available.'}</CardDescription>
                </CardContent>
                <CardFooter className="flex justify-between">
                    <div className="flex gap-2">
                        {project.homepage && (
                            <Button asChild size="sm">
                                <a href={project.homepage} target="_blank" rel="noopener noreferrer">
                                    <ExternalLink />
                                    {translations.projects.live_demo}
                                </a>
                            </Button>
                        )}
                        <Button asChild variant="secondary" size="sm">
                            <a href={project.html_url} target="_blank" rel="noopener noreferrer">
                                <Github />
                                {translations.projects.source}
                            </a>
                        </Button>
                    </div>
                </CardFooter>
                </Card>
            ))}
            </div>
        )}
      </div>
    </motion.section>
  );
}
